@import url('https://fonts.googleapis.com/css?family=Montserrat');

body {
  margin: 0;
  color: rgb(59, 77, 3);
  background-color: rgb(251,243,215);
  font-family: 'Montserrat'
}
/* CSS for the header section */

.header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar { 
  max-width: 1200px;
  max-height: 800px;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  width: 100%;
  
}

.navbar .links a{ 
  padding: 10px;
  text-decoration: none;
  color:rgb(59, 77, 3);
  font-weight: bolder;
}

.navbar .name {
  text-decoration: none;
  font-weight: bolder;
  color: rgb(59, 77, 3);
}

/* CSS for the content section */

.content-body {
  display: flex;
  justify-content: center;
  
}

.text-content {
  display: block;
  max-width: 800px;
  max-width: 800px;
  width: 100%;
  padding: 2rem;
  font-size: 20px;
}

.profile-picture {
  display: block;
  max-width: 350px;
  max-height: 350px;
  width: auto;
  height: auto;
  padding: 2rem;
}

.text-content hr {
  width: 20%;
  margin-left: 0;
}

/* CSS for Projects section */

.content .projects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects h1 {
  display: flex;
  justify-content: center;
  font-weight: bolder;
}

.projects hr {
  width: 20%;
}

.landing-page, .calculator, .weather-app, .flight-finder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 800px;
  color: black;
  margin: 0;
  padding: 1rem
}

.landing-page .links a, .calculator .links a, .weather-app .links a, .flight-finder .links a{
  padding: 1rem;
}


.calculator a img, .landing-page a img, .weather-app a img, .flight-finder a img{
  max-width: 800px;
}

/* CSS for the footer section */

.contactSection h1 {
  display: flex;
  justify-content: center;
}

.contactSection hr {
  width: 20%;
}

.contactSection .links {
  display: flex;
  justify-content: center;
  
}

.contactSection .links div {
  width: 10%;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
  margin:3rem;
  height: 6rem;
  border-radius: 10px;
  background-color: transparent;
  border: 1px solid rgb(59, 77, 3);
}

.my-github, .linkedin, .email {
  display: flex;
  justify-content: center;
}

.email a, .linkedin a, .my-github a {
  display: flex;
}
